import React from 'react';

const MonthButton = (props) => (
  <button onClick={props.onClick}>{props.title}</button>
);

MonthButton.propTypes = {
  title: React.PropTypes.string,
  onClick: React.PropTypes.func,
};

export default MonthButton;



